import {useState, useEffect} from 'react';

import styles from './Header.module.css';

const Header = () => {

    const [showSticky, setShowSticky] = useState(false);
    const [showMenu, setShowMenu] = useState(false);

    useEffect(() => {
        window.addEventListener('scroll', () => {
            (window.scrollY > 0) ? setShowSticky(true) : setShowSticky(false);
        });
    }, []);

    const toggleMenu = () => {
        setShowMenu(!showMenu);
    }

    return(
        <header className={showSticky && styles.sticky}>
            <a href="/portfolio/#banner" class={styles.logo}>Portfolio</a>
            <nav>
                <ul class={`${styles.menu} ${showMenu && styles.open}`}>
                    <li><a href="/portfolio/#aboutme" onClick={toggleMenu}>A propros</a></li>
                    <li><a href="/portfolio/#workexperiences" onClick={toggleMenu}>Expériences</a></li>
                    <li><a href="/portfolio/#projects" onClick={toggleMenu}>Projets</a></li>
                    <li><a href="/portfolio/#skills" onClick={toggleMenu}>Compétences</a></li>
                    <li><a href="/portfolio/#news" onClick={toggleMenu}>Veille technologique</a></li>
                    <li><a href="/portfolio/#contact" onClick={toggleMenu}>Contact</a></li>
                </ul>
            </nav>
            <div class={styles["menu-icon"]} onClick={toggleMenu}>
                <div class={`${styles.line} ${showMenu && styles.open}`}></div>
                <div class={`${styles.line} ${showMenu && styles.open}`}></div>
                <div class={`${styles.line} ${showMenu && styles.open}`}></div>
            </div>
        </header>
    );
};

export default Header;