import AboutText from './AboutText';

import profil from '../../../assets/img/photo.jpg'

import styles from './AboutSection.module.css';

const AboutSection = () => {
    return(
        <section class={styles.aboutme} id="aboutme">
            <div class={styles["section-content-box"]}>
                <AboutText />
                <img class={styles["section-content-img"]} src={profil} alt="Thomas" />
            </div>
        </section>
    );
};

export default AboutSection;