import {Route} from 'react-router-dom';

import Header from './components/utils/Header';
import Footer from './components/utils/Footer';
import MainPage from './pages/MainPage';
import WorkExperienceDetailsPage from './pages/WorkExperienceDetailsPage';

function App() {
	return (
		<div className="App">
			<Header />
			<Route path="/portfolio" exact component={MainPage} />
			<Route path="/portfolio/work-experience/:workExperienceId" component={WorkExperienceDetailsPage} />
			<Footer />
		</div>
	);
}

export default App;
